<template>
  <div>
    <card-container
      title="보상이 확대되길 원하는<br>특정 시간이나 장소가 있으신가요?"
      @openModal="openAttributeHelpModal(when2HelpData)"
      help>
      <checkbox
        v-model="selectedWhen2Data"
        v-for="(item, index) in subAttributes.when2"
        :key="index"
        :title="item.title"
        :tValue="item.code">
      </checkbox>
    </card-container>
  </div>
</template>

<script>
  import cardContainer from '../../../components/card-container.component'
  import checkbox from '../../../../../components/checkbox.component'

  export default {
    props: {
      value: Array
    },
    data () {
      return {
        selectedWhen2Data: [],
        when2HelpData: {
          title: '보상이 확대되길 원하는<br>특정 시간이나 장소가 있으신가요?',
          contents: [
            {title: '주말/휴일', content: '주말 또는 법정공휴일, 근로자의 날 전날 18시 부터 다음날 06시 이내 사고 발생 시<br>보상 확대'},
            {title: '출퇴근 시간', content: '월요일 ~ 금요일의 5시부터 10시, 17시부터 22시 이내 사고 발생 시 보상 확대<br>단, 법정 공휴일 및 근로자의 날 제외'},
            {title: '골프장 가는 길', content: '피보험자동차를 운행 또는 탑승하여 회원제 및 퍼블릭 골프장(골프 연습장 제외)으로<br>이동하는 중 사고 발생 시 보상 확대'},
            {title: '스쿨존', content: '유치원, 초등학교, 특수학교 또는 보육시설의 주 출입문을 중신으로 반경 300m<br>이내 도로 중 어린이 보호구역으로 표시되어 있는 일정 구간에서 사고 발생 시 보상 확대'},
            {title: '안전벨트 착용 중', content: '사고 당시 안전벨트를 착용했음에도 불구하고 피해를 입은 경우 보상 확대'},
            {title: '시간/장소 제약 없이', content: '시간과 장소의 제한 없이 사고가 발생한 경우 언제든지 보상 확대'}
          ]
        }
      }
    },
    methods: {
      openAttributeHelpModal (data) {
        this.$emit('openAttributeHelpModal', data)
      }
    },
    watch: {
      selectedWhen2Data (val) {
        this.$emit('input', val)
      }
    },
    computed: {
      subAttributes () {
        return this.$store.getters.getInsurance('subAttributes')
      }
    },
    components: {
      cardContainer,
      checkbox
    }
  }
</script>

<style scoped lang="scss">
</style>



