<template>
  <div>
    <card-container
      title="어떤사고에 더 든든하고<br>싶으신가요?"
      @openModal="openAttributeHelpModal(when1HelpData)"
      help>
      <checkbox
        v-model="selectedWhen1Data"
        v-for="(item, index) in subAttributes.when1"
        :key="index"
        :title="item.title"
        :tValue="item.code"
      ></checkbox>
    </card-container>
  </div>
</template>

<script>
  import cardContainer from '../../../components/card-container.component'
  import checkbox from '../../../../../components/checkbox.component'

  export default {
    props: {
      value: Array
    },
    mounted () {
      this.selectedWhen1Data = this.value
    },
    data () {
      return {
        selectedWhen1Data: [],
        when1HelpData: {
          title: '어떤 사고에 더 든든하고 싶으신가요?',
          contents: [
            {title: '내 차 운행(탑승) 중 사고', content: '피보험 자동차를 운행 또는 탑승 중 발생한 사고'},
            {title: '다른차 운행(탑승) 중 사고', content: '다른 사람 소유의 자동차를 운행 또는 탑승 중 발생한 사고'},
            {title: '대중교통 이용 중 사고', content: '여객수송용 전철, 지하철 또는 시내/농어촌/마을/고속버스, 일반택시 등의 대중교통을 이용하는 중 발생한 사고'},
            {title: '종교버스 탑승 중 사고', content: '교회(성당)버스에 탑승 중 발생한 사고'},
            {title: '보행 중 사고', content: '보행 중 다른 자동차에 의해 발생한 사고'}
          ]
        }
      }
    },
    methods: {
      openAttributeHelpModal (data) {
        this.$emit('openAttributeHelpModal', data)
      }
    },
    watch: {
      selectedWhen1Data (val) {
        this.$emit('input', val)
      }
    },
    computed: {
      subAttributes () {
        return this.$store.getters.getInsurance('subAttributes')
      }
    },
    components: {
      cardContainer,
      checkbox
    }
  }
</script>

<style scoped lang="scss">
</style>



