<template>
  <div>
    <card-container title="최적의 자동차 보험을 위해<br>설계모드를 선택해 주세요." @openModal="openAttributeHelpModal">
      <button class="selection" @click="selectMode('package')" :class="{active: isPackage}">
        <img src="/static/png/img_mode_package_nor.png" class="img">
        <div class="selection-title">
          패키지 설계
        </div>
        <div class="selection-content">
          나와 유사한 패키지를 선택하여<br>
          쉽고 간편하게 자동차 보험을<br>
          설계할 수 있습니다
        </div>
      </button>
      <button class="selection right" @click="selectMode('design')" :class="{active: isDesign}">
        <img src="/static/png/img_mode_self_nor.png" class="img">
        <div class="selection-title">
          직접 설계
        </div>
        <div class="selection-content">
          보장범위와 조건, 보상한도 등을<br>
          내가 원하는 만큼 지정하여<br>
          설계할 수 있습니다
        </div>
      </button>
    </card-container>
  </div>
</template>

<script>
  import cardContainer from '../../../components/card-container.component'

  export default {
    data () {
      return {
        mode: null
      }
    },
    methods: {
      selectMode (mode) {
        if (this.mode !== mode) {
          this.$emit('selectMode', mode)
          this.mode = mode
        }
      },
      openAttributeHelpModal (data) {
        this.$emit('openAttributeHelpModal', data)
      }
    },
    computed: {
      isPackage () {
        return this.mode === 'package'
      },
      isDesign () {
        return this.mode === 'design'
      }
    },
    components: {
      cardContainer
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../../../style/variable";

  // select-mode
  .selection {
    display: inline-block;
    height: 320px;
    width: 265px;
    border-radius: 8px;
    border: 1px solid $gray;
    text-align: center;
    transition: background-color .2s;
    &.active {
      background-color: $gray;
    }
  }

  .img {
    margin-bottom: 12px;
  }

  .selection-title {
    font-size: 16px;
    margin-bottom: 12px;
  }

  .selection-content {
    font-size: 16px;
  }

  .right {
    float: right;
  }
</style>



