<template>
  <div>
    <card-container title="대인배상I" sub-title="자동차 사고로 다른 사람이 다치고나 사망한 경우 보상합니다.">
      <div>
        <select-slider title="보상한도 설정(의무가입)" v-model="selectedPerson" :disabled="true"
                       tip="대인배상I은 자동차손해보상보장법에서 정한 의무 보험으로 변경이 불가능합니다."
                       :isIcon="true"
                       :data="['2', '1억 5천만원/부상 3천만원']" @clickIcon="openCoverageHelpModal(person1HelpData)"></select-slider>
      </div>
    </card-container>
  </div>
</template>

<script>
  import cardContainer from '../../../components/card-container.component'
  import selectSlider from '../../../../../components/select-slider.component'

  export default {
    data () {
      return {
        input: {},
        selectedPerson: '1억 5천만원/부상 3천만원',
        person1HelpData: {
          title: '대인배상I 보상조건',
          subTitle: '사망/후유장애 최고 1억 5천만원, 부상 최고 3천만원<br>한도 내에서 실제 손해액을 보상합니다.',
          contents: [
            {img: '/static/png/ic_popup_list_01.png', content: '치료비, 위자료, 휴업손해, 상실수익액, 장례비 등을 포함합니다.'},
            {img: '/static/png/ic_popup_list_02.png', content: '무면허, 음주사고 시 300만원의 본인 부담금 부과될 수 있습니다.'}
          ]
        }
      }
    },
    methods: {
      selectParson () {
        this.$emit('selectedPerson', this.selectedPerson)
      },
      openCoverageHelpModal (data) {
        this.$emit('openCoverageHelpModal', data)
      }
    },
    watch: {
      selectedPerson (val) {
        this.selectParson()
      }
    },
    components: {
      cardContainer,
      selectSlider
    }
  }
</script>

<style scoped lang="scss">
</style>



