<template>
  <div>
    <card-container title="대물배상" sub-title="다치거나 사망한 경우 손해를 보상합니다.">
      <div>
        <div class="select-object">
          <select-slider title="보상한도 설정(의무가입)" v-model="selectedObject" :isIcon="true"
                         :data="coverage.object" @clickIcon="openCoverageHelpModal(objectHelpData)"></select-slider>
        </div>
        <div class="select-surcharge">
          <select-slider title="물적사고 할증기준금액 설정" v-model="selectedSurcharge" :isIcon="true"
                         :data="coverage.surcharge" @clickIcon="openCoverageHelpModal(surchargeHelpData)"></select-slider>
        </div>
        <div v-if="!isPackage">
          <item-card title="외제차 충돌 시 보상 확대"
                     question="고액 대물사고시 보상이 확대되도록 조건을 추가하시겠습니까?"
                     description="값비싼 외제차 충돌 시 가입한도 이상의 고액 수리비가 발생할 경우<br>충분한 위험 보장이 아루어지지 않을 수 있습니다."
                     icon="/static/png/img_boxbg_02.png"
                     :value="foreignCarAccident"
                     @click="isForeignCarAccident">
          </item-card>
        </div>
      </div>
    </card-container>
  </div>
</template>

<script>
  import cardContainer from '../../../components/card-container.component'
  import selectSlider from '../../../../../components/select-slider.component'
  import itemCard from './sub-components/item-card.component'

  export default {
    props: {
      isPackage: Boolean,
      object: Object,
      surcharge: Object,
      foreignCarAccident: Boolean
    },
    data () {
      return {
        selectedObject: this.object,
        selectedSurcharge: this.surcharge,
        objectHelpData: {
          title: '대물배상 보상조건',
          subTitle: '대물사고 발생 시 선택하신 한도 내에서 상대방의<br>직접손해와 간접손해를 보상합니다.',
          contents: [
            { img: '/static/png/ic_popup_list_01.png', content: '수리비, 렌트비, 휴차료, 영업손실, 시세하락손실 등을 보상합니다.' },
            { img: '/static/png/ic_popup_list_02.png', content: '무면허, 음주사고 시 300만원의 본인 부담금 부과될 수 있습니다.' },
            { img: '/static/png/ic_popup_list_03.png', content: '무면허 사고는 책임배상 (2천만원) 한도만 보상합니다.' },
            { img: '/static/png/ic_popup_list_04.png', content: '운전자범위, 최저연령을 위반한 경우 보상이 불가합니다.' }
          ]
        },
        surchargeHelpData: {
          title: '물적할증기준금액 보상조건',
          subTitle: '선택하신 한도 내에서 물적 손해액이 발생하면 보험갱신 시<br>보험료가 할증 되지 않습니다.',
          contents: [
            { img: '/static/png/ic_popup_list_05.png', content: "물적사고란 '대물' 및 '자기차량' 사고를 의미합니다" },
            { img: '/static/png/ic_popup_list_06.png', content: '기준금액 이하 사고가 1건인 경우만 등급이 유지됩니다.' },
            { img: '/static/png/ic_popup_list_07.png', content: '단, 최근 3년 이내 다른 건의 사고경력이 있는 경우 <br>보험료가 올라갈 수 있습니다.' }
          ]
        }
      }
    },
    methods: {
      isForeignCarAccident (ForeignCarAccident) {
        this.$emit('selectedForeignCarAccident', ForeignCarAccident)
      },
      selectObject () {
        this.$emit('selectedObject', this.selectedObject)
      },
      selectSurcharge () {
        this.$emit('selectedSurcharge', this.selectedSurcharge)
      },
      openCoverageHelpModal (data) {
        this.$emit('openCoverageHelpModal', data)
      }
    },
    computed: {
      coverage () {
        return this.$store.getters.getInsurance('coverage')
      }
    },
    watch: {
      selectedObject () {
        this.selectObject()
      },
      selectedSurcharge () {
        this.selectSurcharge()
      }
    },
    components: {
      cardContainer,
      selectSlider,
      itemCard
    }
  }
</script>

<style scoped lang="scss">
  .select-object {
    margin-bottom: 45px;
  }
  .select-surcharge {
    margin-top: 30px;
    margin-bottom: 45px;
  }
</style>



